body {
  background-color: #f5f5ec;
  margin: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.dark_mode {
  color: #fff;
  background-color: #5e5c5c;
}

main {
  background-color: #dbd5d5;
  margin-bottom: 180px;
  margin-left: 20%;
  margin-right: 20%;
  box-shadow: 0 2px 4px #0003;
}

h2 {
  margin-bottom: 0;
  padding: 2%;
}

p {
  max-width: 36em;
  margin-top: 0;
  padding: 2%;
}

nav {
  background-color: #850d49;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

h1 {
  color: #fff;
  margin-left: 1%;
}

.navbar li {
  padding: 70px;
  font-size: 1.25em;
  display: inline;
}

.navbar a {
  color: #fff;
  padding: 25px;
  font-weight: bold;
  text-decoration: none;
}

.navbar a:hover {
  color: #000;
  background-color: #be82a9;
}

footer {
  text-align: center;
  color: #fff;
  background-color: #850d49;
  justify-content: center;
  width: 100%;
  display: flex;
  position: fixed;
  bottom: 0;
}

@media (width <= 800px) {
  body {
    flex-direction: row;
    padding-top: 150px;
    display: flex;
  }

  main {
    box-shadow: none;
    background-color: #f5f5ec;
    margin-left: 5%;
    margin-right: 5%;
  }

  nav {
    width: 100%;
    position: fixed;
    top: 0;
  }

  .navbar li {
    padding: 10px;
  }

  .navbar a {
    padding: 1px;
  }

  footer {
    display: none;
  }
}

